@charset "utf-8";

/* Reset */
body,
h1,
h2,
h3,
figure {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  vertical-align: middle;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/* Foundation */
body {
  color: #2d3c49;
  font: 16px "Open Sans", sans-serif;
}

a {
  -webkit-transition: color 0.2s ease;
  -o-transition: color 0.2s ease;
  transition: color 0.2s ease;
}

a:hover {
  color: #7d97ad;
}

/* Header */
.header {
  background: #2d3c49;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 20px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 60px;
}

.header__logo {
  font: 400 1.2em/60px "Open sans", serif;
}

/* Section */
.section {
  margin: 0 auto;
  max-width: 1450px;
  width: 90%;
}

/* Footer */
.footer {
  background: #f1f1f1;
  padding: 50px 0 150px;
  position: relative;
}

.footer__inner {
  margin: 0 auto;
  width: 90%;
  max-width: 1450px;
}

/* Hero */
.hero {
  height: 100vh;
  position: relative;
  margin-bottom: 50px;
}

.hero__title {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #fff;
  font: 400 3em "Pacifico", cursive, sans-serif;
  text-align: center;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 0 20px;
  width: 100%;
}

.hero__img {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 100%;
}

/* Headline */
.headline {
  font: 400 2em "Pacifico", cursive, sans-serif;
  text-align: center;
  margin-bottom: 0.5em;
}

/* Featured work */
.featured-work {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.featured-work__item {
  margin-bottom: 30px;
  width: 100%;
}

/* Work */
.work {
  text-align: center;
}

.work__img {
  border-radius: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  margin-bottom: 20px;
  overflow: hidden;
  width: 100%;
  position: relative;
  z-index: 1;
}

.work__img--border {
  border: 1px solid #ddd;
}

.work__img:hover img {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.work__img img {
  display: block;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  width: 100%;
  height: auto;
  -webkit-transition: -webkit-transform 0.5s ease;
  transition: -webkit-transform 0.5s ease;
  -o-transition: transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}

.work__title {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.work__summary {
  line-height: 1.7;
}

/* Profile */
.profile__bio {
  line-height: 1.7;
  text-align: center;
}

/* Copyright */
.copyright {
  text-align: center;
  position: absolute;
  bottom: 50px;
  width: 100%;
}

@media screen and (min-width: 500px) {
  .hero__title {
    font-size: 4.6785em;
  }

  .headline {
    font-size: 3.125em;
    margin-bottom: 50px;
  }
}

@media screen and (min-width: 600px) {
  .header__logo {
    font-size: 1.5em;
  }
  .featured-work__item {
    width: 47.5%;
  }
}

@media screen and (min-width: 992px) {
  .section {
    margin-bottom: 100px;
  }

  .featured-work__item {
    width: 30%;
  }
}



